:root {
	--color-white:     #fff;
	--color-black:     #000;
	--color-yellow:    #ffff00;
	--color-dark-gray: #10100e;

	--color-blue:   #6a83fb;
	--color-violet: #ae72c2;
	--color-red:    #f75d83;

	--spacing-ultrasmall: 5px;
	--spacing-small:      10px;
	--spacing-medium:     20px;
	--spacing-large:      30px;

	--font-size-main:      16px;
	--font-size-subheader: 18px;
	--font-size-header:    26px;

	--margin-list: 7px;
	--width-max: 1200px;

	--font-bold: 600;
	--transition: .4s;
	--font-family-main: sans-serif;

	--border-radius-small: 3px;
	--border-radius-medium: 5px;

	--text-shadow-yellow:
		0 0 2px var(--color-white),
		0 0 40px var(--color-yellow);

	--text-shadow-blue:
		0 0 2px var(--color-white),
		0 0 40px var(--color-blue);

	--theme-light-header: #902CFF;
	--theme-dark-header: #ca6c6f;
	--theme-neitral-header: #cccc46;
    --theme-default-header: var(--theme-neitral-header);


	--theme-light-bgimage: linear-gradient(to right top, #02214f, #0c3672, #164e98, #1c66c0, #1e80e9);
	--theme-dark-bgimage: linear-gradient(to right top, #be2c2c, #952428, #6d1e22, #47171a, #230f0f);
	--theme-neitral-bgimage: url(@static/bg.jpg);
	--theme-default-bgimage: var(--theme-neitral-bgimage);
}

body {
	margin: 0;
	padding: 0;
	font-family: var(--font-family-main);
	font-size: var(--font-size-main);
	background-color: var(--color-dark-gray);
	background-image: var(--theme-default-bgimage);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top left;
	background-attachment: fixed;
}
img {
	display: block;
}
.header__text {
	line-height: 1.7;
	font-size: var(--font-size-header);
	text-shadow: 0 0 2px var(--color-black);
	color: var(--theme-default-header);
	transition: color var(--transition);
}

/*
Scroll bar
/*/
*{
	scrollbar-color: rgb(33,52,97) rgba(0,0,0, 0.7);
	scrollbar-width: thin;
	
}
::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}

::-webkit-scrollbar-thumb{
	background-color: rgb(33,52,97);
	border-radius: 2px;
}
::-webkit-scrollbar-track{
	background-color: rgba(0,0,0, 0.7) ;
}
::-webkit-scrollbar-corner {
	background-color: rgba(0,0,0, 0.7) ;
}