.list__container {
    column-count: 3;
    margin: 20px 0;
    padding: 0;
    list-style-type: none;    
}
.list__item{

}
.list__item a {
    display: inline-flex;
    color: #000;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}
.person__photo{
    width: 70px;
    height: 70px;
    object-fit: cover;
    object-position: center center;
    border-radius: var(--border-radius-small);
}
.person__name{
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0 var(--spacing-small);
    color: #fff;
    text-shadow: var(--text-shadow-blue);

}
.person__comment{
    color: #fff;
}
@media screen and (max-width: 850px) {
    .list__container{
        column-count: 2;
    }
    
}