.button{
    min-width: 100px;
    padding: 10px;
    border-radius: 3px;
    font-size: 14px;
    font-size: 18px;
    font-family: sans-serif;
    font-weight: 600;
    background: none;
    outline: none;
    transition: 0.4s;
    cursor: pointer;
}
.button:disabled,
.button:disabled:hover {
    color: #9c9c9c;
    background-color: transparent;
    border-color: #9c9c9c;
    cursor: auto;
}
.dark{
    border: 1px solid #fff;
    color: #fff;
}
.dark:hover{
    color: #000;
   background-color: #fff;
}
.light{
    border: 1px solid #000;
    color: #000;
}
.light:hover{
    color: #fff;
   background-color: #000;
}